export default {
	data() {
		return {
			TabList: [
				'清洁间歇导尿推荐方案',
				'预防泌尿系感染'
			],
			activeIndex: 0,
			cleanList: [
				'(q4-6h）自行排尿少于100ml或残余尿量高于300ml时',
				'(q6h）自行排尿 > 100ml，或残余尿量 < 300ml时',
				'(q8h）自行排尿超过200ml，或残余尿量低于200ml时，每8小时导尿一次',
				'(停止导尿）当残余尿 < 100 mL，即为膀胱容量的 10％-20％，连续监测残余量7天，残余量均 < 100 mL，则可停止间歇导尿',
			],
			preventList: [{
					text: '注意手卫生，导尿前，导尿后严格执行七步洗手法',
					active: false
				},
				{
					text: '导尿术护理 （每天）',
					active: false
				},
				{
					text: '会阴部、尿道口的护理 （每天），用温水清洁会阴保持会阴区皮肤清洁干燥',
					active: false
				},
				{
					text: '勤换内裤，穿棉质、透气的内裤',
					active: false
				},
				{
					text: '保持肛门周围清洁干燥',
					active: false
				}

			],
			waterPlanState: true,
			urinatePlanState: false,
			cleanIndex: -1
		}
	},
	onLoad(options){
		this.activeIndex = options.id;
	},
	methods: {
		changeTab(e) {
			this.activeIndex = e;
		},
		radioChange(e) {
			console.log(e);
		},
		waterPlan(e) {
			this.waterPlanState = !this.waterPlanState;
		},
		urinatePlan(e) {
			this.urinatePlanState = !this.urinatePlanState;
		},
		selectRadio(e) {
			this.cleanIndex = e;
		},
		preventIndex(e) {
			this.preventList[e].active = !this.preventList[e].active;
		}
	},
}
